<script>
import DataService from '../../service/DataService'
import Treeselect from '@riophae/vue-treeselect'
import '@riophae/vue-treeselect/dist/vue-treeselect.css'
export default {
    components: {
        'tree-view': Treeselect
    },
    props: {
        filepath: {
            type: String,
        }
    },
    data() {
        return {
            Xdata: [],
            AssetType: [],
            StatusListData: [],
            BarcodeType: [],
            AuditData: [],
            ImageCaptureData: [],
            AssetFrequencyList: [],
            CalibrationData: [],
            LocationValue: null,
            options: [],
            dData: [],
            UserId: '',
            ExcelListData: [],
            ProductType: [],
            StatusData: [],
            BarcodeData: [],
            AuditRecord: [],
            ImageCaptureRecord: [],
            CalibrationRecord: [],
            DepreciationRecord: [],
            LocationData: []

        }
    },
