                            vm.ShowAlert(RetMeg, "success", true, vm.$t("Alert"));
                            vm.$router.push({ path: "/AssetCatalog/Index" });

                        })
                    }
                    else {
                        vm.ShowAlert(vm.$t('ValidationHighlights'), "failure", vm.$t('Alert'));
                        vm.isLoading = false;
                    }
                })
            }
